html,
body,
#root,
.App {
  height: 100%;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  background-color: #ffffff;
  overflow: auto;
}

.mainHeader {
  position: relative;
  padding: 0.8em 0;
  /* background-color: #2B303A; */
  box-shadow: 0px 4px 6px #00000015;
  z-index: 1;
  font-size: small;
  font-family: "Comic Sans MS", sans-serif;
  flex-wrap: nowrap;
}

.headerContainer {
  display: inline-flex;
}

.mainHeader .logo {
  height: 35px;
  margin-left: 2.5em;
  margin-right: 0.5em;
}

.headerContainer .userMsg {
  position: absolute;
  right: 10px;
  margin-top: 10px;
}

.headerContainer .messageContainer {
  margin-top: 15px;
}

.pageContainer {
  font-size: 0.9rem;
  background-color: #f5f5f5;
  align-items: flex-start;
  display: flex;
  min-height: 100vh;
}

.pageContainer > Form {
  width: 100%;
  height: 100%;
  position: center;
}
.pageContainer #header {
  font-weight: bold;
  font-size: 1.2rem;
  position: relative;
  margin-left: 200px;
}
.navbar {
  grid-column: 1/2;
  display: flex;
  flex-direction: column;
  list-style: none;
  background-color: #ffffff;
  min-width: 70px;
  min-height: inherit; /*TODO fix*/
}

.navOption > a {
  display: block;
  padding: 25px 0;
  text-align: center;
  text-decoration: none;
  font-size: 0.9rem;
  color: #666;
  border-style: solid;
  border-width: 1px;
  border-color: #eee;
}

.navOption > a:hover {
  background-color: #f5f5f5;
  border-right: none;
}

.selected {
  background-color: #f5f5f5;
  border-right: none;
  font-weight: 600;
}

/*=========================================================================*/
/*====================          Login  page        ========================*/
/*=========================================================================*/

.loginForm {
  background-color: #ffffff;
  overflow: auto;
}

.titleHeader {
  font-family: "Comic Sans MS", sans-serif;
  text-align: center;
  color: black;
  font-size: 30px;
  flex: 1;
}

.subTitle {
  /* align-self: center; */
  text-align: center;
  font-size: 18px;
  color: #a5a5a5;
  flex: 1;
}

.subTitle2 {
  text-align: left;
  font-size: 1.2rem;
  color: #a5a5a5;
  flex: 1;
  width: 50%;
  padding-left: 20%;
}

.pageContainer2 {
  text-align: center;
  width: 100%;
  background-color: #ffffff;
}

.floatLeft {
  float: left;
  width: 50%;
  background-color: #ffffff;
  height: fit-content;
}

.floatRight {
  float: right;
  width: 60%;
  background-color: #ffffff;
  height: fit-content;
}

.formDisplay {
  text-align: center;
}

input[type="text"] {
  width: 60%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

input[type="password"] {
  width: 60%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

input[type="email"] {
  width: 60%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

input[type="submit"] {
  background-color: rgb(85, 162, 239);
  color: white;
  padding: 15px 80px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
  text-align: center;
}

input[type="submit"]:hover {
  background-color: #b82b2b;
}

form a {
  color: rgb(0, 174, 239);
  text-decoration: none;
  font-weight: bold;
}

.formError {
  text-align: center;
  font-size: 1.2rem;
  color: red;
  flex: 1;
}
/*Setting page*/
.dynamic-delete-button {
  cursor: pointer;
  position: relative;
  top: 4px;
  font-size: 24px;
  color: #999;
  transition: all 0.3s;
}
.dynamic-delete-button:hover {
  color: #777;
}
.dynamic-delete-button[disabled] {
  cursor: not-allowed;
  opacity: 0.5;
}

/* profile page */
.profileHeadings {
  text-align: left;
  font-size: 2rem;
  font-weight: 700;
  color: #666;
  margin-left: 10px;
}

.profileInfo {
  text-align: left;
  font-size: 1.5rem;
  font-weight: 700;
  color: #666;
  padding-left: 30px;
  padding-top: 20px;
}

.profileInfo .currentInfo {
  font-size: medium;
  font-weight: 500;
  color: black;
  /* background-color: white;
  border: 1px solid #85C1E9; */
  margin-right: 40px;
  padding-left: 20px;
}

.grid-container {
  height: 90vh;
  width: 200vh;
  display: grid;
  grid-template-columns: 35% 65%;
  grid-gap: 10px;
  background-color: #a2d9ce;
  padding: 10px 10px 10px 10px;
}

.grid-container .rightCol {
  padding-top: 30px;
}
.nameInfo {
  display: grid;
  grid-template-columns: 50% 50%;
}

.grid-container > div {
  background-image: url("asserts/background.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 20px;
  /* padding-right: 20px; */
}

.editButtonBox {
  margin-top: 100px;
  margin-bottom: 30px;
}
.updateButtonBox {
  transform: translate(3%, 0);
}

.avatarBox {
  transform: translate(35%, 35%);
}

.avatar-uploader {
  width: 128px;
  height: 128px;
}
/* --------------------------------Loading page ---------------------------*/
.loading {
  margin: 300px auto;
  width: 100px;
  height: 100px;
  display: flex;
  transform: rotate(180deg);
}

.loadingContainer {
  height: 90vh;
  width: 200vh;
  display: block;
}
.loading-item {
  width: 20px;
  height: 100px;
}
.loading-one {
  background-color: #2983bb;
  animation: loading 1.5s linear infinite 0.8s;
}
.loading-two {
  background-color: #229453;
  animation: loading 1.5s linear infinite 0.6s;
}
.loading-three {
  background-color: #74787a;
  animation: loading 1.5s linear infinite 0.4s;
}
.loading-four {
  background-color: #fba414;
  animation: loading 1.5s linear infinite 0.2s;
}
.loading-five {
  background-color: #de1c31;
  animation: loading 1.5s linear infinite;
}
@keyframes loading {
  0% {
    height: 20px;
  }
  50% {
    height: 100px;
  }
  100% {
    height: 20px;
  }
}
